@import "./../../../scss/abstracts/elements/elements";
@import "./../../../scss/abstracts/mixins/mixins";
@import "./../../../scss/abstracts/variable/variable";

.aboutProduct {
    display: flex;
    justify-content: space-around;
    margin: 45px 0 80px;

    .bigCard {
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
    }

    .bigImage {
        margin-top: 64px;
        // max-width: 500px;
        // width: 150%;
        max-height: 375px;
    }

    .lastPrice {
        @include font4(30px, 36px, $Barlow, $main-color);
        // font-weight: normal;
        opacity: 0.6;
        position: relative;

        &::after {
            position: absolute;
            display: inline-block;
            content: "";
            background-color: $main-color;
            width: 140px;
            height: 2px;
            bottom: 15px;
            left: 5px;
            transform: rotate(172deg);
        }
    }

    .price {
        @include font4(40px, 48px, $Barlow, $main-color);
        font-weight: bold;
        margin: 12px 0 17px;
    }

    .message {
        @include font4(15px, 18px, $SFProRegular, $sub-color);
    }

    // ------------infoCard
    .infoCard {
        position: relative;
    }

    .title {
        @include font4(30px, 36px, $SfProBold, $main-color);
        margin-bottom: 10px;
    }

    .code {
        @include font4(15px, 18px, $SFProRegular, $main-color);
    }

    .images {
        display: flex;
        align-items: flex-end;
        margin: 22px 0 35px;
    }

    .like,
    .recs {
        margin-right: 25px;
    }

    .recs {
        display: flex;
        align-items: flex-end;
    }

    .recs,
    .rec {
        margin-right: 4px;
    }

    // ------------table
    .table {
        display: table;
        max-width: 436px;
        margin-bottom: 20px;
        border-collapse: collapse;
    }

    tr {
        border-bottom: 1px solid rgba(0, 0, 0, .12);
        // opacity: 0.6;
    }

    th {
        text-align: left;
        @include font4(20px, 24px, $SFProRegular, $main-color);
        font-weight: 400;
        opacity: 0.6;

        &:hover {
            text-decoration: underline;
            text-decoration-color: $sub-color;
            opacity: 1;
        }

        &:active {
            font-weight: bold;
            // border-bottom: 3px solid $sub-color;
        }
    }

    td {
        @include font4(20px, 24px, $SFProRegular, $main-color);
    }

    #rightcol {
        width: 242px;
        padding-left: 93px;
    }

    #leftcol,
    #rightcol {
        &:hover {
            text-decoration: underline;
            text-decoration-style: 5px solid;
            text-decoration-color: $sub-color;
        }
    }

    th,
    td {
        padding: 15px 0px 6px;
    }

    @media screen and(max-width:992px) {
        .bigCard {
            margin: 0;
            justify-content: center;
        }

        .bigImage {
            max-width: 80%;
        }

        th {
            padding-right: 15px;
        }
    }
}


